"use client";

import { Search } from "lucide-react";
import LogoImage from "@/assets/images/website_logo.png";
import {
  ArrowRight,
  BagIcon,
  LoveIcon,
  UserIcon,
  WalletIcon,
} from "../../../assets/svg/svg_icon";
import Link from "next/link";
import { navLinks } from "../../../core/routes/nav_links";
import { appRoute, cartRoute, checkoutRoute } from "@/core/routes/routeNames";
import { navItems } from "./data/mega_menu.data";
import useWebsiteHeaderHook from "../hooks/website_header.hook";

const WebsiteHeaderComponent = () => {
  const {
    showMegaMenu,
    setShowMegaMenu,
    activeLeftItem,
    setActiveLeftItem,
    wishlistCount,
    router,
    pathname,
    categories,
    activeMenuData,
    handleUserIconClick,
    handleCartIconClick,
    isAuthenticated,
    userFirstName,
    isDashboard,
  } = useWebsiteHeaderHook();

  return (
    <header className="bg-white border-b border-gray-200">
      <div className="container mx-auto px-4 py-4">
        <div className="flex items-center justify-between gap-6">
          {/*==== Logo ====*/}
          <img
            onClick={() => router.push(appRoute)}
            src={LogoImage.src}
            alt="logo"
            className="w-20 h-20 cursor-pointer"
          />

          {/*==== Search Bar ====*/}
          <div className="flex-1 max-w-xl">
            <div className="flex items-center border border-gray-300 rounded-2xl overflow-hidden">
              <input
                type="text"
                placeholder="Search for a product"
                className="flex-1 px-4 py-4 focus:outline-none"
              />
              <button className="px-3 text-gray-400">
                <Search className="w-6 h-6" />
              </button>
            </div>
          </div>

          {/*==== Navigation Links ====*/}
          <nav className="flex items-center gap-8 text-lg font-medium text-tertiary-color">
            {navLinks.map((nav) => {
              const isActive =
                pathname === nav.path ||
                pathname.startsWith(`${nav.path}/`) ||
                (nav.name === "Products" &&
                  (pathname === cartRoute || pathname === checkoutRoute));

              return (
                <Link
                  key={nav.path}
                  href={nav.path}
                  className={`text-lg font-medium transition-colors hover-gradient-text ${isActive ? "gradient-text" : "text-tertiary-color"
                    }`}
                >
                  {nav.name}
                </Link>
              );
            })}
          </nav>

          {/*==== Action Buttons ====*/}
          <div className="flex items-center gap-4">
            <button className="relative text-gray-700 hover:text-emerald-700 cursor-pointer">
              <LoveIcon />
              {wishlistCount > 0 && (
                <span className="absolute -top-1 -right-1 w-4 h-4 bg-emerald-700 text-white text-[10px] rounded-full flex items-center justify-center">
                  {wishlistCount}
                </span>
              )}
            </button>

            {!isDashboard && (
              <button className="relative text-gray-700 hover:text-emerald-700 cursor-pointer">
                <WalletIcon />
              </button>
            )}

            <button
              onClick={handleCartIconClick}
              className="text-gray-700 hover:text-emerald-700 relative cursor-pointer"
            >
              <BagIcon />
              <span className="absolute top-1.5 -right-0.5 w-4 h-4 bg-emerald-700 text-white text-[10px] border border-white rounded-full flex items-center justify-center">
                8
              </span>
            </button>

            <div className="flex items-center gap-3">
              <button
                onClick={handleUserIconClick}
                className={`transition-all cursor-pointer ${isDashboard
                  ? 'text-[#E26E00] scale-110'
                  : 'text-gray-700 hover:text-[#E26E00]'
                  }`}
              >
                <UserIcon stroke="currentColor" />
              </button>

              {isAuthenticated && isDashboard && (
                <div className="flex flex-col items-end -space-y-1">
                  <p className="text-[15px] text-gray-500 font-medium">Welcome back!</p>
                  <p className=" text-lg font-bold text-gray-900 leading-tight">{userFirstName}</p>
                </div>
              )}

            </div>

            {!isDashboard && (
              <button className="w-full p-0.5 border backdrop-blur-[10px] border-[#003625] rounded-[18px] transition disabled:opacity-50 disabled:cursor-not-allowed cursor-pointer">
                <div className="w-45 bg-primary-color text-white py-4 rounded-[14px] font-semibold">
                  Sell Now
                </div>
              </button>
            )}
          </div>
        </div>
      </div>

      {/*==== Category Navigation ====*/}
      {!isDashboard && (
        <div
          className="relative px-4"
          onMouseEnter={() => setShowMegaMenu(true)}
          onMouseLeave={() => setShowMegaMenu(false)}
        >
          <div className="container mx-auto py-3">
            <div className="flex gap-10">
              {categories.map((category: string, index: number) => (
                <span
                  key={index}
                  onMouseEnter={() => setActiveLeftItem(category)}
                  className="text-sm text-light-color font-medium cursor-pointer hover:text-black"
                >
                  {category}
                </span>
              ))}
            </div>
          </div>

          {/* ===== Mega Menu ===== */}
          {showMegaMenu && (
            <div
              onMouseEnter={() => setShowMegaMenu(true)}
              onMouseLeave={() => setShowMegaMenu(false)}
              className="absolute left-0 top-full w-full bg-white border-t border-gray-200 shadow-lg z-50"
            >
              <div className="container mx-auto py-10 flex gap-12">
                {/* Left column */}
                <div className="w-1/4 space-y-5 border-r border-gray-200 pr-8">
                  {navItems.map((item) => {
                    const Icon = item.icon;
                    const isActive = activeLeftItem === item.name;

                    return (
                      <div
                        key={item.id}
                        onMouseEnter={() => setActiveLeftItem(item.name)}
                        className={`text-lg cursor-pointer flex justify-between ${isActive
                          ? "text-[#0C0F16] font-medium"
                          : "text-gray-700 hover:text-[#0C0F16]"
                          }`}
                      >
                        <p className="flex gap-3">
                          {Icon && (
                            <Icon stroke={isActive ? "#0C0F16" : "#4B5563"} />
                          )}
                          {item.name}
                        </p>

                        <span>
                          <ArrowRight />
                        </span>
                      </div>
                    );
                  })}
                </div>

                {/* Right columns */}
                <div className="flex gap-24">
                  {/* Column 1 */}
                  <div className="space-y-3">
                    {activeMenuData?.categories?.[0]?.subCategories?.map(
                      (sub: { id: string; path: string; name: string }) => (
                        <Link
                          key={sub.id}
                          href={sub.path}
                          className="block text-gray-700 hover:text-black cursor-pointer"
                        >
                          {sub.name}
                        </Link>
                      )
                    )}
                  </div>

                  {/* Column 2 */}
                  <div className="space-y-3">
                    {activeMenuData?.categories?.[1]?.subCategories?.map(
                      (sub: { id: string; path: string; name: string }) => (
                        <Link
                          key={sub.id}
                          href={sub.path}
                          className="block text-gray-700 hover:text-black cursor-pointer"
                        >
                          {sub.name}
                        </Link>
                      )
                    )}
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      )}
    </header>
  );
};

export default WebsiteHeaderComponent;
