"use client";

import { useState } from "react";
import { usePathname, useRouter } from "next/navigation";
import { useSelector } from "react-redux";
import secureLocalStorage from "react-secure-storage";
import { RootState } from "@/core/redux/stores/store";
import { cartRoute, dashboardRoute, loginRoute } from "@/core/routes/routeNames";
import { navItems } from "../layout/data/mega_menu.data";

const useWebsiteHeaderHook = () => {
  const [showMegaMenu, setShowMegaMenu] = useState(false);
  const [activeLeftItem, setActiveLeftItem] = useState<string | undefined>();

  const wishlistCount = useSelector(
    (state: RootState) => state.wishlist.items.length
  );

  const router = useRouter();
  const pathname = usePathname();

  // Define the type for navItems if possible, otherwise use 'any'
  type NavItem = { name: string;[key: string]: any };

  // Top level categories (same as before but now from navItems)
  const categories = navItems.map((item: NavItem) => item.name);

  // Find active mega menu data
  const activeMenuData = navItems.find((item: NavItem) => item.name === activeLeftItem);

  // Check if user is authenticated and get their name
  const userDataRaw = secureLocalStorage.getItem("user_data");
  const userData = userDataRaw ? JSON.parse(userDataRaw as string) : null;
  const isAuthenticated = !!userData;

  // Extract first name from backendUser if available
  const userFirstName = userData?.backendUser?.firstName || userData?.displayName?.split(' ')[0] || "User";

  // Check if current page is part of the dashboard/portal
  const isDashboard = pathname?.startsWith("/buyer_portal");

  // Handle user icon click
  const handleUserIconClick = () => {
    if (isAuthenticated) {
      // User is authenticated - redirect to dashboard
      router.push(dashboardRoute);
    } else {
      // User is not authenticated - store current path and redirect to login
      if (pathname && !pathname.startsWith("/auths")) {
        secureLocalStorage.setItem("return_url", pathname);
      }
      router.push(loginRoute);
    }
  };

  // Handle cart icon click
  const handleCartIconClick = () => {
    router.push(cartRoute);
  };

  return {
    showMegaMenu,
    setShowMegaMenu,
    activeLeftItem,
    setActiveLeftItem,
    wishlistCount,
    router,
    pathname,
    categories,
    activeMenuData,
    handleUserIconClick,
    handleCartIconClick,
    isAuthenticated,
    userFirstName,
    isDashboard,
  };
};

export default useWebsiteHeaderHook;
